import Vue from 'vue'
import Router from 'vue-router'
import Anbani from '@/components/Anbani'
import Converter from '@/components/Converter'
import Keyboard from '@/components/Keyboard'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '*',
      redirect: '/'
    },
    {
      path: '/converter',
      name: 'Converter',
      component: Converter,
      alias: '/'
    },
    {
      path: '/keyboard',
      name: 'Keyboard',
      component: Keyboard
    }
  ],
  mode: 'history'
})



